.b-soc-btns {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style: none;
  list-style-position: outside; }

.b-soc-btns__item {
  display: inline-block;
  vertical-align: middle; }
  .b-soc-btns__item + .b-soc-btns__item {
    margin-left: 24px; }

.b-soc-btn__link {
  -webkit-transition: color 300ms;
  transition: color 300ms; }
  .b-soc-btn__link:hover, .b-soc-btn__link:focus {
    color: #6c9c5a; }
  .b-soc-btn__link:active {
    color: #39642a; }
  .b-soc-btns--grey .b-soc-btn__link:hover, .b-soc-btns--grey .b-soc-btn__link:focus {
    color: #4e8839; }
  .b-soc-btns--grey .b-soc-btn__link:active {
    color: #39642a; }
  .b-soc-btns--opacity .b-soc-btn__link {
    opacity: 0.5;
    -webkit-transition: opacity 300ms, color 300ms;
    transition: opacity 300ms, color 300ms; }
  .b-soc-btns--opacity .b-soc-btn__link:hover, .b-soc-btns--opacity .b-soc-btn__link:focus {
    opacity: 1;
    color: inherit; }
  .b-soc-btns--opacity .b-soc-btn__link:active {
    color: #39642a; }



